.imageUploadBtn{
  margin:0 0 9px ;
  background-color: #F3731E;
  color:white;
  width:59px;
  text-align:center;
  border-radius: 2px;
  padding:3px;
  caret-color: transparent;
}

.uploadButton{
    font-size: 14px;
    font-weight: 600;
    line-height: 19px;
    font-family: Open Sans;  
    letter-spacing: 0px;
    color: #0072C6;
    opacity: 1;
    text-align: center;
    margin-left: 125px;
    cursor: pointer;	
  }
  

.imageUploadBtn input[type="file"]{
 display:none;
}

::-webkit-file-upload-button{
  font-size: 14px;
  color: #337AB7;
  border: 1px solid #337AB7;
  border-radius: 2px 0 0 2px;
  border-radius: 2px 0px 0px 2px;
  width:7em;
  padding:6px;
  display:inline-block;
  background-color: white;
}
::-webkit-file-upload-button:focus{
}

.imageUploadBtn2{
    border: 1px dashed #C4C4C4;
    border-radius: 2px;
    height:24px;
    margin:0 0 9px ;
    caret-color: transparent;
    padding:5px 8px;
    display:flex;
    justify-content: space-between;
    text-decoration: underline;
    align-items: flex-end;
    width:100%;
  }

.deleteIcon{
    display: none;
}
.imageUploadBtn2:hover .deleteIcon{
    display: block;
}

.fileUploadParent{
    position:relative;
    display:flex;
    width:100%;
    height:24px;
    z-index: 1;
    border: 1px solid #C4C4C4;
  }
.fileUploadParent .text{
  background-color: white;
  color:#C4C4C4;
  line-height: 24px;
  padding:0 0 0 8px;
  font-size: 12px;
  min-width:100%;
  display:flex;
  justify-content: space-between;
  align-items:center;
}
.fileUploadParent .icon{
  position: relative;
  left:1px;
  border: 1px solid #C4C4C4;
  height:24px;
  width:24px;
  background: #fff;
  padding:0px 5px;
  display:flex;
  align-items: center;
  justify-content: center;
}
.fileUploadParent input[type="file"]{
    border: 1px solid #D4D4D4;
    border-radius: 0 2px 2px 0;
    border-radius: 0px 2px 2px 0px;
    font-size: 12px;
    color: #4A4A4A;
    position:absolute;
    left:0;
    z-index:55;
    width:100%;
    height:24px;
    opacity:0;
    filter: alpha(opacity=0);
    cursor:pointer;
  }
  .fileUploadParent input[type="file"]:focus{
    opacity:0 !important;
  }
.overflowedText{
  width: 90%;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    color: #000000de;
}