.headerContainer{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.pagePropButton.pagePropButton.pagePropButton{
    font-size: 11px !important;
    font-weight: bold;
    border-radius: 4px !important;
    border: 1px solid #C4C4C4 !important;
    /* padding: 5px !important; */
    background-color: white;
}

.collapseItems{
    display: flex;
    justify-content: space-between;
    margin: 8px 4px 8px 4px;
    align-items:center;
}

.selectTemplateContainer:hover{
    cursor: pointer;
}

.inputBoxStyle {
    width: 30px;
    height: 24px;
    margin:0px 4px !important;
    text-align: center;
    
}

.gradientInputBoxStyle.gradientInputBoxStyle.gradientInputBoxStyle {
    width: 40px;
    height: 19px !important;
    /* margin:0px 4px !important; */
    text-align: left;
    border:none !important;
}

.gradientInputBoxStyleForm.gradientInputBoxStyleForm.gradientInputBoxStyleForm {
    width: 80px;
    height: 19px !important;
    /* margin:0px 4px !important; */
    text-align: left;
    border:none !important;
}

.customPropertyContainer.customPropertyContainer.customPropertyContainer{
    display: flex;
    position: relative;
    border: 1px solid #C4C4C4;
    align-items: center;
    border-radius: 2px;
    width:100px;
    margin:5px;
}

.differentPaddingContainer{
    display: flex;
    margin-left: 5px;
}

.comboGradientAngle.comboGradientAngle.comboGradientAngle {
    width: 154px;
}