.backdropDiv { 
   width: 70vw;
   height: 90vh;
   position: absolute;
   background: #fff 0 0 no-repeat padding-box;
   box-shadow: 0 0 10px rgba(81,81,81,.5019607843137255);
   opacity: 1;
   top: 50vh;
   left: 50vw;
   transform: translate(-50%, -50%);
 }
 @media (max-width: 1024px) {
   .backdropDiv { 
      top: 50%;
   }   
 }

