.backdropDiv { 
    width: 90%;
    height: 394px;
    /* display: flex;
    flex-direction: row; */
    position: relative;
    background: #FFFFFF 0% 0% no-repeat padding-box;
    box-shadow: 0px 0px 10px #51515180;
    opacity: 1;
 }
 .backdropHeader { 
    width: 100%;
    height: 50px;
    padding: 16px;
    display: flex;
    align-items: center;
    font: normal normal 600 12px/16px Open Sans;
    color:#000000;
    letter-spacing: 0px;
    opacity: 1;
    margin:0px !important;
    border-bottom: 1px solid #E4E4E4;
 }
 .backdropBody { 
    width: 100%;
    height: 294px;
    padding: 16px;
    display: flex;
    margin:0px !important;
 }
 .backdropLeftBody { 
    width: 20%;
    max-width:20%;
    min-height: 100%;
    display: flex;
    padding: 0px;
    flex-direction: column;
    border: 1px solid #F0F0F0;
    opacity: 1;
 }
 .backdropLeftBodyHeading{ 
    font: normal normal normal 12px/17px Open Sans !important;
    color: #606060;
    padding: 12px;
    letter-spacing: 0px;
 }
 .editingToolOptions{
    max-height: 30px;
    height: 30px;
    display: flex;
    align-items: center; 
    font: normal normal 600 12px/17px Open Sans !important;
    color: #606060;
    padding: 0px 12px;
    letter-spacing: 0px;
 }
 .selectedEditingToolOption { 
    background: #0072C60A 0% 0% no-repeat padding-box;
    
 }
 .editingToolOptionsText { 
    font: normal normal 600 12px/17px Open Sans !important;
    color: #606060;
    margin-left: 12px;
    letter-spacing: 0px;
 }
 .selectedEditingToolOptionText { 
    color: #0072C6 !important;
 }
 .backdropCenterBody { 
    width: 30%;
    max-width:30%;
    min-height: 100%;
    padding: 0px 16px;
 }
 .backdropCenterBodyHeading { 
    font: normal normal 600 12px/17px Open Sans !important;
    color: #606060;
    letter-spacing: 0px;
 }
 .resizeImageDiv  { 
   display: flex;
   align-items: center;
   margin: 6px auto;
}
.resizeOptionInput {
   width: 90px;
   height: 24px;
   display: flex;
   margin-left: auto;
   background: #FFFFFF 0% 0% no-repeat padding-box;
   color: #000000;
   border-radius: 2px;
   outline: #0072C6;
   opacity: 1;
   position: relative;
}
.resizeOptionInputropdown { 
   width: 90px !important;
   margin-left: auto;
   border: 1px solid #C4C4C4;
}
.resizeOptionInputRadio { 
   margin-right: 6px;
}
.resizeOptionHiddenRadio { 
   margin-right: 6px;
   visibility: hidden;
}
.resizeOptionInputText { 
   width: 100% !important;
   height: 100%;
   padding-left: 6px;
   border:none;
   border-radius: none;
   outline: none;
}
.widthTypeSpan { 
   /* width: 15%; */
   display: flex;
   align-items: center;
   padding-right: 6px;
   position: absolute;
   right: 5px;
   height: 2.335rem;
}
 .resizeImageMessage {
   font: normal normal normal 10px/16px Open Sans !important;
   background: #F0F0F0 0% 0% no-repeat padding-box;
   border: 1px solid #C4C4C4;
   border-radius: 2px;
   margin-top: 12px;
   padding: 6px 14px; 
   letter-spacing: 0px;
   color: #000000;
   /* opacity: 0.4; */
 }
 .cropImgCol { 
    margin-top: 12px;
    height:150px;
    padding: 0px;
  }
  .cropRatioDiv {
    width: 100%; 
    height:120px;
    border-bottom: 1px solid #E4E4E4;
 }
 .cropImageDiv { 
    widows: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
 }
 .cropRatioText { 
    margin-bottom: 0px !important;
    font: normal normal normal 12px/17px Open Sans !important;
    color: #000000;
    letter-spacing: 0px;
 }
 .cropRatioDropdown { 
    width: 120px;
    height: 24px;
    background: #FFFFFF 0% 0% no-repeat padding-box;
    border: 1px solid #C4C4C4;
    color: #000000;
    border-radius: 2px;
    outline: #0072C6;
    opacity: 1;
 }
 .cropImageSize { 
   margin: 1rem 0px;
}
 .selectedCropRatio { 
    background: #0072C60A 0% 0% no-repeat padding-box;
}
.cropImageDiv  {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: #FFFFFF 0% 0% no-repeat padding-box;
}
.cropImageDetails {
   height: 14px;
   display: flex;
   align-items: center;
   justify-content: space-between;
}
.widthHeightSpan { 
    margin-left: 6px;
    font: normal normal normal 10px/15px Open Sans !important;
    color: #c4c4c4;
    letter-spacing: 0px;
}
.verticalSeparator { 
   height: 14px;
   margin-left: 6px;
   border-left: 1px solid #C4C4C4 ;
}
 .rotateFlipCol { 
    width:70%;
    margin-top: 12px;
    padding: 0px;
    display: flex;
    justify-content: start;
    gap: 10px;
 }
 .rotateFlipImgDiv  { 
    width:33px;
    height:30px;
    position: relative;
    display: flex;
    align-items:center;
    justify-content: center;
    background: #FFFFFF 0% 0% no-repeat padding-box;
    border: 1px solid #C4C4C4;
    border-radius: 2px;
    opacity: 1;
 }
 .selectedRotateFlipOption  { 
    border: 1px solid #0072C6;
 }

 .backdropRightBody { 
    max-width:50%;
    width: 50%;
    height: -webkit-fill-available;
    height: -moz-available;
    overflow: auto;
    padding:15px;
    display: flex;
    background: #F0F0F0 0% 0% no-repeat padding-box;
    border: 1px solid #C4C4C4;
    opacity: 1;
 }
 .backdropFooter { 
    width: 100%;
    height: 50px;
    margin:0px !important;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    position: absolute;
    bottom: 0;
    background: #F5F5F5 0% 0% no-repeat padding-box;
    opacity: 1;
 }
 .cancelButton {
    height:28px;
    background: #FFFFFF 0% 0% no-repeat padding-box !important;
    border: 1px solid #C4C4C4 !important;
    border-radius: 2px !important;
    font: normal normal 600 12px/17px Open Sans !important;
    letter-spacing: 0px;
    margin-right:16px !important;
    color: #606060 !important;
    text-transform: none !important;
    opacity: 1;
}
.saveButton{
    height:28px;
    background: #0072C6 0% 0% no-repeat padding-box !important;
    border-radius: 2px !important;
    text-transform: none !important;
    margin-right:16px !important;
    font: normal normal 600 12px/17px Open Sans !important;
    color: #FFFFFF !important;
}
.resizeButton{
    width:90px;
    height:26px;
    background: #FFFFFF 0% 0% no-repeat padding-box !important;
    border: 1px solid #0072C6 !important;
    border-radius: 0px 2px 2px 0px !important;
    text-transform: none !important;
    font: normal normal 600 10px/9px Open Sans !important;
    letter-spacing: 0px;
    color: #0072C6 !important;
    opacity: 1;
}

.rotateFlipImgDiv .tooltiptext {
   visibility: hidden;
   text-align: center;
   padding-left: 4px;
   padding-right: 4px;
   background: #1616169C 0% 0% no-repeat padding-box;
   box-shadow: 0px 0px 6px #00000029;
   border-radius: 5px;
   font: normal normal normal 8px/16px Open Sans;
   letter-spacing: 0px;
   color: #FFFFFF;
   white-space: nowrap;
   display: flex;
   align-items:center;
   position: absolute;
   z-index: 1;
   top: 140%;
   opacity: 1;
 }
 
 .rotateFlipImgDiv:hover .tooltiptext {
   visibility: visible;
 }
 /* --------------------------------------------- Extra CSS -------------------------------------------------*/
 .parentSection {
   width: 80%;
   height:300px;
   margin: 2rem 2rem;
   background: #0072C60A 0% 0% no-repeat padding-box;
 }
 .tooltiptext2 {
   visibility: visible;
 }
 .tooltiptext1 {
   visibility: visible;
 }
 .tooltiptext1 {
   visibility: visible;
 }