.plus_css{
    position: absolute;
    right: 0px;
    top: 0px;
    z-index: 5;
    background-color: white;
    display:none;
}

.minus_css{
    position: absolute;
    right: 0px;
    bottom: 0px;
    z-index: 5;
    background-color: white;
    display:none;
}
.col_css:hover .plus_css, .col_css:hover .minus_css{
    /* border:1px dashed #0087F7; */
    display:block;
}

.col_css:active .plus_css, .col_css:active .minus_css{
    display:block;
}
.resizer{
    width: 10px;
    height: 10px;
    background: white;
    border: 1px solid grey;
    position: absolute;
    right: -6px;
    z-index:1;
    background-color: white;
    top: 44%;
    cursor: w-resize;
    opacity:0;
}
.resizer2{
    width: 10px;
    height: 10px;
    background: white;
	background-color: white;
    border: 1px solid grey;
    position: absolute;
    z-index:1;
	left: 47%;
    bottom: -6px;
    cursor: s-resize;
    opacity:0;
}

.col_css:active > .resizer, .col_css:hover > .resizer{
    opacity:1;
}
.col_css:active > .resizer2, .col_css:hover > .resizer2{
    opacity:1;
}

.resizerLabel{
    width: 20px;
    height: 15px;
    font-size: 12px;
    background: #4286f4;    
    position: absolute;
    right: -10px;
    z-index: 1;
    color: white;
    top: 3px;
    cursor: w-resize;
    opacity: 0;
    border-radius: 2px;
    line-height: normal;
    text-align: center;
    font-family: "Open Sans";
}
