.sketchPickerStyle{
    position:absolute;
    z-index:2;
    left: -120px;
    top: 30px;
}
.sketchPickerStyleGradient{
    position:absolute;
    z-index:2;
    left: -70px;
    top: 30px;
}
.sketchPickerStyleArabic{
    position:absolute;
    z-index:2;
    left: 0px;
    top: 30px;
}
.inputStyle{        
    width: 76px;
    height: 20px;
    border: 1px solid #C4C4C4;
    border-radius: 2px;
}
.colorStyle{
        height:12px;
        width:12px;
        padding:4px;
        margin:4px;
        border: 4px solid #E8E8E8;
        border-radius: 2px;
        display:inline-block;
        vertical-align:middle;
        cursor:pointer;
}
.colorParentDiv{
	display: flex;
    align-items: center;
    font-size: 11px;
    position: relative;
}
.colorParentDiv input{
    text-transform: uppercase;
    padding-left: 3px;
}

/* COlor pIcker Specifics */

.sketch-picker{
    display:flex;
    flex-direction: column;
    padding:0 10px 0 10px !important;
    margin-bottom: 1px;
}

.sketch-picker>:nth-child(1){
    order:2;
    padding-bottom:75px !important;
    margin-bottom:5px;
}
.sketch-picker>:nth-child(1) > div{
    height:75px;
    padding:0;
}

.sketch-picker>:nth-child(2){
    order:3;
}

.sketch-picker>:nth-child(3){
    order:4;
}

.sketch-picker>:nth-child(3) * input {
    width:100% !important;
}

.sketch-picker>:nth-child(4){
    order:1;
    margin-bottom: 5px;
}
.sketch-picker>:nth-child(4) > div{
    height:32px !important;
    width:32px !important;
}

.sketch-picker>:nth-child(4) > :nth-child(n+11){
    display:none;
}