.container {
 text-align: left;
 width: 100%; 
 border-radius: 2px; 
 overflow-x: hidden;
}

.functionButton {
    border: none;
    border-radius: 2px !important;
    background-color: #0072c6;
    height: 25px !important;
    width: 25px !important;
    color: white;
    font-size: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .defaultbtn {
    background: none;
    border: none;
    /* text-decoration:solid; */
    font: normal normal 600 12px/17px "Open Sans";
    color: #0072c6;
    cursor: pointer;
    padding: 0px !important;
  }
  
  .row {
    padding: 6px 0px;
    align-items: center;
    font: normal normal 600 12px/15px Open Sans !important;
  }

  .addRow{
    border: 1px solid #F5F5F5;
    margin: 0px 8px 0px 0px;
    background-color: #F5F5F8;
    padding: 6px 10px;
  }

  .availableFunctionRow{
    border: 1px solid #F5F5F5;
    margin: 1px 0px;
    padding: 10px;
    display: flex;
    justify-content: space-between;
  }

  .hoveredBusinessFuction{
    padding-left: 5px;
    display: flex;
    justify-content: space-between;
    width: 70px;
  }

  .hoveredDraftBusinessFuction{
    padding-left: 5px;
    display: flex;
    justify-content: space-between;
    width: 50px;
  }

  .defaultbtn {
    background: none;
    border: none;
    text-decoration: underline;
    font: normal normal 600 12px/17px "Open Sans";
    color: #0072c6;
    cursor: pointer;
    /* padding-left: 200px !important; */
  }

  .saveBtn {
    background-color: #0072C6;
    color: white;
    border: none;
    border-radius: 3px;
    cursor: pointer;
  }

  .inputBody{
    width: 100%;
    height: 280px;
    border: 1px solid grey;
    overflow-y: auto;
    margin-top: 15px;
    /* opacity: 0.3; */
  }

  .parameterHeader {
    font-weight: bold !important;
    font-size: 14px !important;
    width: 170px;
  }

  .horizontalLine{
    width: 100%;
    height: 1px;
    background-color: gray;
    margin-top: 13px;
    opacity: 0.3;
  }

  .optionsPopUp {
    /* position: absolute; */
    margin: -5px 0px;
    width: 60px;
    height: 30px;
    background: #F5F5F8;
    /* box-shadow: 0px 0px 6px #00000029;
    border:1px solid #ffffff ;
    border-radius: 2px; */
    font: normal normal normal 12px/17px Open Sans;
    letter-spacing: 0px;
    color: #6b6b6b;
    text-align: left;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 10px;
    right: 2em;
    transform: translate(0px, 5px);
    z-index: 99999;
    cursor: pointer;
  }
  
  .optionsPopUp > span {
    padding: 5px 0;
  }
  .optionsPopUp > span:hover {
    color: #000000;
    /* transform: scale(1.1); */
    /* padding:5px 5px; */
  }

  .optionsPopUp1 {
    /* position: absolute; */
    margin: -5px 0px;
    width: 95px;
    height: 60px;
    background: #FFFFFF;
    font: normal normal normal 12px/17px Open Sans;
    letter-spacing: 0px;
    color: #6b6b6b;
    text-align: left;
    display: flex;
    flex-direction: column;
    justify-content: center;
    /* padding: 10px; */
    right: 0em;
    transform: translate(0px, 5px);
    z-index: 99999;
    cursor: pointer;
  }
  
  .optionsPopUp1 > span {
    padding: 5px 0;
  }
  .optionsPopUp1 > span:hover {
    color: #000000;
    background: #F5F5F8;
    /* transform: scale(1.1); */
    /* padding:5px 5px; */
  }

  .functionLabel {
    width: 140px;
    display: block;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }