.properties_container_main .tabPropertiesHeader{
    color:#FA6420;
    font-weight: 600;
    font-size: 14px;
    line-height: 19px;
    padding:9px 10px;
    border-left: 4px solid #FA6420;
    display:flex;
    justify-content: space-between;
}
.properties_container_main .tabPropertiesBody,.properties_container_main .tabBehaviourBody,.properties_container_main .activeTabBody,.properties_container_main .inactiveTabBody,.properties_container_main .tabAppearance{
    display:flex;
    justify-content: space-between;
    padding: 8px 0;
}
/* tabLayoutBody */
.properties_container_main .tabLayoutBody{
    /* margin:1em 0;
    padding:0.5em;
    border: 1px solid #DADADA;
    border-radius: 2px; */
    cursor:pointer;
    margin:5px;
}
.properties_container_main .tabLayoutBody:hover{
    /* border: 1px solid #FA6420;
    padding:18px 0; */
}
/* tabLabelsBody */
.properties_container_main .tabLabelsBody{
    caret-color: transparent;
}
.properties_container_main .tabLabelsBody .addTabBtn {
    width:100%;
    font-weight:600;
    color:#FA6420;
    border: 1px solid #FA6420;
    border-radius: 2px;
    background: #FFFFFF;
    padding: 5px 0;
}
.properties_container_main .tabLabelsBody .addIcons{
    text-align: right;
    display:block;
    padding: 1em 0;
    font-weight:600;
    align-content: center;
}
.properties_container_main .tabLabelsBody input{
    width:100%;
    border: 1px solid #C4C4C4;
    border-radius: 2px;
}
.properties_container_main select:active, .properties_container_main select:hover,.properties_container_main select:focus,.properties_container_main option:hover,.properties_container_main input:hover,.properties_container_main input:focus,.properties_container_main input{    
    /* border-style: inset; */
    outline-color: #C4C4C4;
}
.properties_container_main select{
    border: 1px solid #C4C4C4;
    border-radius: 3px;
    width:10em;
    padding:0 4px;
}
.properties_container_main select::after{
    content:">";
    transform: rotate("90deg");
    position: absolute;
    z-index: 4;
}
.properties_container_main .selectedStyle{
    border: 1px solid #FF6600;
    padding:13px 1px 16px;
}
.properties_container_main .tabNameInputs{
    height: 26px;
    border: 1px solid #C4C4C4;
    border-radius: 2px;
    margin:9px 0;
}
.properties_container_main input::placeholder{
    font-size: 12px;
    color: #202020;
    padding:5px;
    text-transform: capitalize;
    font-style: normal;
}