
.slideBar{
  display: inline-block;
  height:20px;
  caret-color: transparent;
}
.switch {
  margin-left: 9px;
position: relative;
display: inline-block;
width: 28px;
height: 14px;
vertical-align: middle;
margin-bottom: 0;
}
.switchArabic {
  margin-right: 9px;
position: relative;
display: inline-block;
width: 28px;
height: 14px;
vertical-align: middle;
margin-bottom: 0;
}
.switch input { 
opacity: 0;
width: 0;
height: 0;
}
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #c4c4c4;
  -webkit-transition: .5s;
  transition: .5s;
  font-size:8px;
  text-align: center;
}
.slider:before {
  position: absolute;
  bottom:2px;
  content: "";
  height: 10px;
  width: 10px;
  left: 2px;
  background-color: white;
  -webkit-transition: .5s;
  transition: .5s;
}
.slider2:before {
  content:"X";
}
input:checked + .slider {
  background-color: #0072C6;
}
input:checked + .slider:before {
  height:"12px";
  -webkit-transform: translateX(14px);
  -ms-transform: translateX(14px);
  transform: translateX(14px);
}
input:checked + .slider2:before {
  content:"\2713";
}  
.slider.round {
  border-radius: 11px;
}  
.slider.round:before {
  border-radius: 50%;
}
.sliderText{
  font-size: 8px;
  text-align:center;
  margin:auto;
  height:8px;
  width:auto;
}
.sliderText:before{
  position:absolute;
  content:"X";
  top:2px;
  right:6px;
}
input:checked ~ .sliderText:before{
  opacity:0;
}
input:checked ~ .sliderText:after{
  content:"\2713";
  position:absolute;
  top:2px;
  left:6px;
}

.redCrossStepIcon {
  vertical-align: unset !important;
}

.quickToolSwitchStyle {
  margin-bottom: 0 !important;
}