@import "/src/Resources/rsuite/rsuite.css";
@import "/src/Resources/react_checkbox/react_checbox.css";

body {
  margin: 0;
}



.formBuilder_main_app *:not(.childmf-formb *) {
  box-sizing: border-box;
  /* color:#000000; */
  caret-color: transparent;
  /* font-size: 12px; */
}

.formBuilder_main_app td:not(.childmf-formb *),
.formBuilder_main_app th:not(.childmf-formb *) {
  padding: 0;
}

.formBuilder_main_app p + p:not(.childmf-formb *) {
  margin-top: 8px;
}

.formBuilder_main_app .container {
  min-width: 100%;
  /* padding:0 !important; */
}

.formBuilder_main_app select:focus {
  /* outline-color: #0072c6; */
  outline: 1px solid #0072c6 !important;
}


.formBuilder_main_app select,
.formBuilder_main_app input {
  border: 1px solid #c4c4c4;
  border-radius: 2px;
  background-color: #fff;
  color:#000;
}
.formBuilder_main_app ::placeholder {
  font-style: normal;
  color: #202020;
}
.formBuilder_main_app svg {
  cursor: pointer;
}

.formBuilder_main_app input,
.formBuilder_main_app textarea,
.formBuilder_main_app .sun-editor-editable * {
  caret-color: #000000 !important;
}
.formBuilder_main_app ::placeholder {
  font-style: normal;
  color: #000000;
}
.formBuilder_main_app textarea {
  resize: none;
}

.formBuilder_main_app b,
.formBuilder_main_app strong {
  font-weight: 600 !important;
}

/* .formBuilder_main_app div > * {
  font-family: "Open Sans", "Gill Sans", sans-serif;
} */
.sun-editor .se-container {
   z-index: 0 !important; 
}


.formBuilder_main_app textarea:focus,
.formBuilder_main_app input:not([type="checkbox"]):not([type="radio"]):focus,
/* button:focus, */
.formBuilder_main_app select:focus {
  outline-color: #0072c6;
  outline-width: 0.1px;
  -moz-outline-color: #0072c6 !important;
  -moz-outline-width: 0.1px !important;
  opacity: 1;
  color: #000;
  border:1px solid #0072c6 !important
}
.formBuilder_main_app select:focus {
  outline-color: #c4c4c4;
  opacity:1;
}
/* select option{
  background-color: white;
}
select:focus option {
  background-color: white;
} */

.formBuilder_main_app .Loading_fallback {
  font-size: 24px;
  text-align: center;
  align-self: center;
  color: rgb(0, 114, 198, 0.7);
  margin-top: 25vh;
}
.formBuilder_main_app div:focus {
  border: 1px solid #0072c6 !important;
}
.formBuilder_main_app div:not(#mainCanvas *):not(#canvasId *):focus {
  border: 1px solid var(--brand_color1) !important;
  outline:unset !important
}
.formBuilder_main_app div:not(#mainCanvas *):not(#canvasId *):focus-visible {
  border: 1px solid var(--brand_color1) !important;
  outline:unset !important

}
.formBuilder_main_app button:not(#mainCanvas *):not(#canvasId *):focus-visible {
  border: 1px solid var(--brand_color1) !important;
  outline:unset !important

}
.formBuilder_main_app span:not(#mainCanvas *):not(#canvasId *):focus-visible {
  border: 1px solid var(--brand_color1) !important;
  outline:unset !important

}
.formBuilder_main_app .MuiTouchRipple-child {
background-color:unset !important
}

.formBuilder_main_app div:focus > .elemDeleteControl {
  display: block;
}

.propertiesBody{
  border: 1px solid transparent;
}
.propertiesBody label {
  margin: 0;
}
.formBuilder_main_app .MuiIconButton-label {
  padding-right: 0px !important;
}
.formBuilder_main_app .MuiOutlinedInput-root.Mui-focused .MuiOutlinedInput-notchedOutline {
  /* border:0;*/
  /* border: 1px solid #c4c4c4 !important; */
}
.formBuilder_main_app .MuiSelect-outlined.MuiSelect-outlined:focus {
  border: 1px solid transparent;
  background-color: #ffffff;
}
.formBuilder_main_app .slick-slide.slick-active:focus {
  border: none !important;
}

.formBuilder_main_app .iconImg {
  vertical-align: text-top;
}
.formBuilder_main_app .shade-main_pagessubforms{
  position: absolute;
    z-index: 998;
    left:0%;
    top:0%;
    height:100%;
    width:100%;
    background-color: rgba(0, 0, 0, 0.5);
    opacity: 0.3;
    /* background: transparent; */
}
.formBuilder_main_app .statustagPositive{
 
  font-size: 12px;
  color: #368211;
  letter-spacing: -0.5px;
  line-height: 20px;
  background: #DBF5CE;
  border-radius: 4px;
  height:24px;
  padding: 2px 8px;
  margin-left: 16px
}

.formBuilder_main_app .statustagNegative{
 
  font-size: 12px;
  color: #D10816;
  letter-spacing: -0.5px;
  line-height: 20px;
  background: #FFD9DB;
  border-radius: 4px;
  height:24px;
  padding: 2px 8px;
  margin-left: 16px;
  
}
.formBuilder_main_app .statustagNetural{

  font-size: 12px;
  color: #B56600;
  letter-spacing: -0.5px;
  line-height: 20px;
  background: #FFF3CE;
  border-radius: 4px;
  height:24px;
  padding: 2px 8px;
  margin-left: 16px
  
}

#searchInput {
  border: none !important;
}


#surveyButtons {
  margin: 0px !important;
}

#addButtonCommonStyle {
  width: 98%;
  height: 27px;
  background: #FFFFFF 0% 0% no-repeat padding-box !important;
  border: 1px solid #0072C6;
  color: #0072C6 !important;
  border-radius: 2px;
  opacity: 1;
  margin:12px 4px;
}

#addButtonCommonStyle:hover{
  background: #0072C6 !important;
  color: white !important;
}

.sliderInput {
  border: none !important;
  border-bottom: 1px solid black !important;
  padding: 0;
  background-color: transparent;
  padding-left: -50px;
  margin-left: -50px;
}


#sliderId {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.spacingThemeControls .InputLable #sliderId .sliderInput:focus .sliderInput:focus-visible {
  border: none !important;
  border-bottom: 1px solid black !important;
  -webkit-box-shadow: none !important;
  outline: none !important;
}

.MuiDialog-root button.MuiPickersDay-day{
  height: 36px !important;
  margin: 0 2px !important;
}

.prefixSlider {
  position: relative;
  right: -15px;
  margin-right: 2px;
  color: black;
}

.overrideGlobalCSSFocus.overrideGlobalCSSFocus.overrideGlobalCSSFocus.overrideGlobalCSSFocus.overrideGlobalCSSFocus.overrideGlobalCSSFocus input:focus {
  border: none !important;
  box-shadow: none !important;
}

@media only screen and (max-height: 400px){
  .formBuilder_main_app .collapseCard  .configurationHeader {
    padding: 0 !important;
    display: flex !important;
    height: 20px !important;
    padding-left: 5px !important;
  }
  .formBuilder_main_app .collapseCard  .configurationHeaderDiv{
    margin: 0 !important;
  }
  .formBuilder_main_app .pagessubforms_main *{
    font-size: 8px !important;
  }
  .formBuilder_main_app .pagessubforms_main button{
    height: 16px !important;
    margin: 0 !important;
  }
  .formBuilder_main_app .pagessubforms_main img{
    height: 8px !important;
    width: 8px !important;
  }
  .formBuilder_main_app .pagessubforms_main input[type=text]{
    height: 20px !important;
  }
  .formBuilder_main_app .pagessubforms_main .pagessubforms_header{
    padding: 2px 8px !important;
  }
  .formBuilder_main_app .pagessubforms_main .pagessubforms_body{
    min-height: -webkit-fill-available !important;
    min-height: -moz-available !important;
  }
  
}
.formBuilder_main_app input[type=text], .formBuilder_main_app input[type=password] {
  padding: 0 .667rem !important;
  height: 2.333rem !important;
  line-height: 2.333rem !important;    
}

