    border-radius: 2px;
    display: flex;
    justify-content:center;
    align-items: center;
    opacity: 1;
    margin: auto;
    cursor: grab;
}
.ControlsWrapper:active {
    cursor: grabbing;
    cursor: -moz-grabbing;
    cursor: -webkit-grabbing;
}
