.DesignerHeader {
    height: 35px;
    /* border-bottom:0.5px solid #FA6400; */
    caret-color:transparent;
    background: #F8F8F8 0% 0% no-repeat padding-box;
    box-shadow: 0px 3px 6px #00000029;
    justify-content: center !important;
    position: relative;
    /* z-index:1; */
    opacity: 1;
}
.btnDivider {
    border: 1px solid #DADADA !important;
    opacity: 1 !important;
    height: 21px !important;
    margin-left:8px !important;
    margin-right:8px !important;
    margin-top: auto !important;
    margin-bottom: auto !important;
    width: 0px !important;
    background-color: #DADADA !important;
}
.themeEditRow {
    margin: 0px !important;
    height: 35px;
}
.themeEditCol1 {
    display: flex;
    max-width: 214px !important;
    width:214px !important;
    height: 28px;
    margin: auto 8px;
    padding: 0px 0px 0px 0px !important;
    align-items: center;
    background: #FFFFFF 0% 0% no-repeat padding-box;
    border: 1px solid #D3D3D3;
    border-radius: 2px;
    
    opacity: 1;
}
.themeEditCol2 {
    display: flex;
    max-width: 35px !important;
    width: 35px !important;
    height: 100%;
    margin: auto 20px auto auto;
    padding: 0px !important;
    align-items: center;
    justify-content: center;
    background: #FFFFFF 0% 0% no-repeat padding-box;
    opacity: 1;
}
.selectedThemeName {
    display: inline-block;
    margin: 0 auto;
    font: normal normal 600 11px/15px Open Sans !important;
    color: #000000;
}
.themeChangeButton {
    text-align: center;
    text-decoration: underline !important;
    font: normal normal 600 10px/14px Open Sans !important;
    letter-spacing:  0px !important;
    color: #0F6DBD !important;
    padding: 0px !important;
    opacity: 1;
    border : none !important;
    float: right !important;

}
.themeEditCol2Selected {
    border-top: 2px solid #0072C6;
}
.themeIcon{
    height: 28px;
    width: 28px;
    margin: 0 2px;
    background: #FFFFFF 0% 0% no-repeat padding-box;
    border: 1px solid #CDCDCD;
    opacity: 1;
    padding: 5px;
    border-radius: 3px;
}
.themeNameIcon{
    height: 100%;
    width: 28px;
    margin: 0 2px;
    background: #FFFFFF 0% 0% no-repeat padding-box;
    /* border: 1px solid #CDCDCD; */
    opacity: 1;
    padding: 2px;
    border-radius: 3px;
}
.propertyIcon{
    /* top: 0px;
    left: 1286px; */
    /* width: 41px;
    height: 41px; */
    /* background: var(--unnamed-color-ffffff) 0% 0% no-repeat padding-box; */
    height: 28px;
    width: 28px;
    margin: 0 9px;
    margin-right: 5px;
    background: #FFFFFF 0% 0% no-repeat padding-box;
    border: 1px solid #CDCDCD;
    opacity: 1;
    padding: 3px;
    border-radius: 3px;
}
.btnDividerTranslation.btnDividerTranslation.btnDividerTranslation.btnDividerTranslation{
    margin-right: 24px !important;
    margin-left: unset !important;
}
.IconbuttonTranslation.IconbuttonTranslation.IconbuttonTranslation.IconbuttonTranslation{
    margin-left: 40px !important;
    margin-right: unset !important;
}