.controlLabel{
    font-size:14px;
    color:#0072C6;
    font-weight:600;
    height:34px;
    padding:8px;
    width: 100%;
    /* border-bottom: 1px solid #d2d2d2; */
    /* border-left:4px solid #FA6400; */
    display:flex;
    justify-content: start;
    background: #FFFFFF 0% 0% no-repeat padding-box;
    caret-color: transparent;
}
.controlLabel span{
    cursor:pointer; 
}

.controlSet__Label {
    color: #0072C6;
    font-family: Open Sans;
    font-weight: 600;
    font-size: 13px;
    text-decoration: underline;
}