.dataObjects{
    /* min-height: 90vh;
    height: 100%; */
    /* overflow-y: scroll; */
    width:100%;
}

.dataObjectPortal{
    width: 231px;
    height: 28px;
    background: #ffffff;
    border-radius: 2px;
    opacity: 1;
    margin-left: 10px;
    margin-top: 10px;
    margin-bottom: 8px;
    /* padding: 10px; */
    padding-bottom: 5px;
    padding-left: 5px;
    padding-top: 4px;
}

.dataObjectlabel{
    text-align: left;
    font: normal normal normal 12px/17px Open Sans;
    letter-spacing: 0px;
    color: #606060;
    opacity: 1;
}

.downArrowClass{
    float: right;
    margin-right: 6px;
    border:1px solid blue;
    padding-right:3px;
    cursor:pointer;
}

.portalProcessClass{
    display: flex;
    align-items: center;
    padding: 12px;
    margin-bottom: 2px;
    cursor: pointer;
    height: 28px;
}

.portalProcessAfterClickClass{
    display: flex;
    align-items: center;
    padding: 12px;
    margin-bottom: 2px;
    cursor: pointer;
    background-color: #E6F1FA;
    height: 28px;
}

.portalProcessTickClass{
    float: right;
    margin-right: 2px;
}

.currentDataObject{
    text-align: left;
    font: normal normal 800 12px/17px Open Sans;
    letter-spacing: 0px;
    color: #000000;
}

.dataObjectScreenClass{
  margin-left:10%;
  margin-top:60%;  
}

.dataObjectScreenContent{
    width:204px;
    text-align:center;
}

.dataObjectScreenIcon{
    margin-left:24%
}

.buttonClass{
    width: 59px;
    height: 28px;
}
.menuDataObjectsModel .menuDataObjectsModel::after {
    content: " ";
    position: absolute;
    bottom: 100%;  /* At the top of the tooltip */
    left: 50%;
    margin-left: -5px;
    border-width: 5px;
    border-style: solid;
    border-color: transparent transparent rgb(232, 226, 226) transparent;
  }
