.IFormDesignerLayout {
    padding: 0;
    margin: 0;
    position: relative;

}
.IFormDesignerLayout .formControlsSection {
    min-height: 93vh;
    max-height: 93vh;
}
.Controls{
    padding-left: 0px;
}
.Properties{
    padding-right: 0px;
}
.leftNavigator {
    min-height: 90vh;
    height: 100%;
    padding-left: 0px; 
    padding-right: 0px;
}

.leftFixedDiv{
}


.CanvasStyle{
    overflow-y: hidden;
    display: flex; 
    justify-content: center;
    align-items: center;
    min-height:100%;
    flex-basis: 0;
    flex-grow: 1;
    max-width: 100%;
    position: relative;
    width: 100%;
    width:90vw;
    padding-right: 5px;
    padding-left: 5px; 
}


.CanvasStyle2{    
    overflow-y: auto;
    display: flex;
    justify-content: center;
    min-height: 90vh;
    flex-basis: 0;
    flex-grow: 1;
    max-width: 100%;
    position: relative;
    width: 100%;
    padding-left: 5px;  
    padding-right:10px;  
}

.CanvasStyle3{    
    overflow-y: auto;
    display: flex;
    justify-content: center !important;
    min-height: 90vh;
    flex-basis: 0;
    flex-grow: 1;
    max-width: 100%;
    position: relative;
    margin-left: 15%;
    margin-right: 15%;
    width: 70%;
    padding-right: 5px;
    padding-left: 5px;
    background-color: #47964d;
}

.canvas3{
    top: 0px;
    left: 0px;
    position: relative;
    width: 50%;
    height: 50%;
    background-color: darkgoldenrod;
}

.CanvasBackground1{
    min-height: 95vh;
    background-color: #FF0000;
    opacity: 5%;
    position: absolute;
    left: 20px;
    right: 20px;
    z-index: -1;
}

.CanvasBackground2{
    min-height: 95vh;
    position: absolute;
    z-index: -1;
    border-left: 2px solid pink;
    border-right: 2px solid pink;
}

.CanvasBackground3{
    min-height: 95vh;
    position: absolute;
    z-index: -1;
    border-left: 8px solid #9100FF;
    border-right: 2px solid lightgray;
}


.newFormControlsDrawer{
    display: flex;
    flex-wrap: wrap;
}
.spaceForPinnedLeftNavigator{
    width: 250px;
}
.spaceForPinnedRightNavigator{
    width: 250px;
}
