.d-tree-container{
    list-style: none;
    padding: 0;
}

.doListItem{
  border: 1px solid transparent !important;
}
.doListItem:hover{
  border: 1px solid var(--brand_color1) !important;
}

.d-tree-node{
    padding: 0.75rem 1.25rem;
}

.d-tree-toggler.active{
    transform: rotate(45deg);
}

.question-popup{

    position: fixed;

    top:0em;

    right:0em;

    height:100vh;

    width:30vw;

    z-index: 1000;

    /* padding:1.5em 2em; */

    background: #FFFFFF;

    /* box-shadow: 0 3px 6px 0 rgb(0,0,0,0.16); */

    /* font-size: 14px; */

    /* font-weight: 600; */

    /* overflow: auto; */

    display: flex;

    flex-direction: column;

    /* overflow-y: scroll !important; */

}

.question-popup-safari{
  position: fixed;
  top:0em;
  right:0em;
  height:100vh;
  width:20vw;
  z-index: 1000;
  background: #FFFFFF;
  display: flex;
  flex-direction: column;
}

.shade-main2{

    position: absolute;
  
    z-index: 5;
  
    left:0%;
  
    top:0%;
  
    height:100vh;
  
    width:100%;
  
    /* background-color: rgba(0, 0, 0, 0.5); */
  
    opacity: 0.3;
  
    background: #000000;
  
  }

  input[type='checkbox'].inputMargin{
    margin: 0 8px 0 0 !important;
    width: 12px !important;
  }

  .footerWrap {
    width: 100%;
    height: 62px;
    color: gray;
    position: fixed;
    bottom: 0;
    box-shadow: 0px 1px 10px grey;
    background-color: white;
  }
  .footerWrapMac {
    width: 100%;
    height: 62px;
    color: gray;
    position: fixed;
    bottom: 10px !important;
    box-shadow: 0px 1px 10px grey;
    background-color: white;
  }