.tooltipMain{
    pointer-events: auto;
    position:relative;
    caret-color:transparent;
    margin: 0px 2px;
    display:inline-block;
}
.toolTip{
    padding:0 3px;
    display:inline-block
}
.tooltip2 {
    position:absolute;
    z-index: 1000; 
    width: 0;
    height: 0;
    opacity:0;
}
.tooltipIcon:hover ~ .tooltip2,.tooltipMain:hover .tooltip2  {
    position: absolute;
    left:0; 
    width: auto;
    max-width: 200px;
    height: auto;
    padding: 5px;
    text-align: left;
    font: normal normal normal 12px/17px Open Sans;
    letter-spacing: 0px;
    color: #000000;
    opacity: 1;
    background: #fff;
    box-shadow: 0px 3px 6px #00000029;
    border: 1px solid #E7E7E7;
    word-break: normal;
    
}
.tooltip2:after {
    content: "";
    position: absolute;
    
    border-width: 10px;
    border-style: solid;
    border-color: #fff transparent transparent transparent;
   
}

/* Towards Right */
.tooltipLeft{
    right:2em;
    transform: translate(0,-100%);
}
.tooltipLeft:after{
    right:-1.5em;   
    transform:rotate(-90deg);
}

/* Towards Left */

.tooltipRight{
    left:2em;
    transform: translate(0,-100%);
}
.tooltipRight:after{
    left:-1.5em;    
    transform:rotate(90deg); 
}


 /* Towards Top */
.tooltipBottom{
    top:2em;
    transform: translate(-30%,0);
}
.tooltipBottom:after{
    top:-1.5em;
    transform:rotate(180deg);
    left:30%; 
}

/* Towards Bottom */
.tooltipTop{
    bottom:2em;
    transform: translate(-30%,0);
}
.tooltipTop:after{
    bottom:-1.5em;
    left:30%;
}