.eventButton {
  border: none;
  border-radius: 2px !important;
  background-color: #0072c6;
  height: 25px !important;
  width: 25px !important;
  color: white;
  font-size: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.row {
  padding: 6px 0px;
  align-items: center;
  font: normal normal 600 12px/15px Open Sans !important;
}

.controlbutton {
  border: none !important;
  border-radius: 2px !important;
  background-color: #0072c6 !important;
  height: 28px !important;
  color: white !important;
  margin-left: 14px !important;
  font-size: 12px !important;
  padding: 5px 6px !important;
}
.controlbuttonclicked {
  border: none !important;
  border-radius: 2px !important;
  background-color: #d3d3d3 !important;
  height: 28px !important;
  color: black !important;
  margin-left: 14px !important;
  font-size: 12px !important;
  padding: 5px 6px !important;
}

/* .controlbutton:hover {
  background-color: #0072c6 !important;
} */

.controlbuttonclicked:focus {
  background-color: #d3d3d3 !important;
  color: black !important;
  opacity: 1;
}

.tableelementhead {
  font: normal normal 600 13px/17px Open Sans !important;
  border-right: 1px solid #c4c4c4;
  padding: 0 !important;
}
.tableelement {
  font: normal normal normal 14px/19px Open Sans !important;
  border-right: 1px solid #c4c4c4;
  padding: 0 !important;
}
.lastelement {
  font: normal normal normal 14px/19px Open Sans !important;
  padding: 0 !important;
}

.toggleclass {
  justify-content: center !important;
}
.togglechild:checked {
  background-color: #0072c6 !important;
}
.centertoggle {
  margin: auto;
}

.defaultbtn {
  background: none;
  border: none;
  text-decoration: underline;
  font: normal normal 600 12px/17px "Open Sans";
  color: #0072c6;
  cursor: pointer;
}
.uploadbtn {
  background: #ffffff 0% 0% no-repeat padding-box;
  border: 1px solid #338ed1;
  border-radius: 2px;
  cursor: pointer;
  opacity: 1;
  color: #0072c6;
  padding: 5px 8px;
  font: normal normal 600 12px/17px "Open Sans";
}

.addrowbtn {
  border: none;
  background: none;
  color: #0072c6;
  text-decoration: underline;
  margin-right: 20px;
  font: normal normal 600 12px/17px "Open Sans";
}

.eventheader {
  cursor: pointer;
  max-height: 36px;
  border-bottom: 1px solid #c4c4c4;
  display: flex;
  align-items: center;
  padding: 9px 11px;
  background: #f5f5f5 0% 0% no-repeat padding-box;
  font: normal normal 600 14px/19px "Open Sans";
  justify-content: space-between;
}

.controlbtn {  
  font: normal normal 600 12px Open Sans;
  line-height:unset !important;
  width: 100%;
  cursor: pointer;
  border: none;
  background: none;
  padding: 10px 0 5px 0;
}

.tableclass {
  border: 1px solid #c4c4c4;
  margin-bottom: 16px;
}

.labelintable {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  padding: 11px;
  font: normal normal 600 14px/19px Open Sans;
  justify-content: space-between;
}


.mainfooter {
  width: 952px;
  height: 50px;
  color: gray;
  position: fixed;
  bottom: 0;
  box-shadow: 0px 1px 10px grey;
  background: #f5f5f5 0% 0% no-repeat padding-box;
  margin-top: 50px;
}

.divinfooter {
  min-width: 160px;
  margin-top: 11px;
  float: right;
  padding: 0 40px;
}

.cancelbtn {
  /* width: 54px; */
  height: 28px;
  font-size: 12px;
}
.donebtn {
  background-color: #0072c6 !important;
  color: white !important;
  /* width: 43px !important; */
  height: 28px !important;
  margin-left: 12px !important;
  font-size: 12px !important;
}

.eventview {
  width: 95%;
  padding: 24px 33px;
  /* margin-bottom: 20px;  */
  height: 90vh;
}
.eventview * {
  font-family: Open Sans;
}

.eventCategory {
  min-height: 65px;
  display: flex;
  flex-direction: column;
  /* margin-bottom: 24px; */
  padding: 10px 0;
}
.eventCategory:hover {
  background-color: #0072C610;
  cursor: pointer;
}
.selectedEventCategory {
  background-color: #0072C620;
}
.MuiAutocomplete-clearIndicator {
  display : none
}