.config {
    border-radius : unset !important;
    caret-color: transparent;
    margin:0;
    height: 20 !important;
}
.config > *{
    margin:1px 0;
}
.config *{
    caret-color: #000000;
}
.configHeader {
    height:26px;
    text-align: left;
    padding: 6px 0px 5px 8px !important;
    font-family: 'Open Sans';
    font-size: 12px;
    font-weight: 600;
    line-height: 14px;
    letter-spacing: 0px;
    color: #000000;
    opacity: 1;
    margin:0;
    cursor: pointer;
    background-color:#FFFFFF;
    border-bottom: 0.5px solid #C4C4C4;
}
.configHeaderDiv{
    min-width: 243px;
    padding-right: 10px;
    display:flex;
    justify-content: space-between;
    align-items: center;
    color:#202020;
}
.headSpan1 {
    text-align: center;
    font: normal normal 600 11px/16px Open Sans;
    letter-spacing: 0px;
    color: #C4C4C4;
    opacity: 1;
    margin-right: 12px;
}
.headSpan2 {
    display: inline-block;
    max-width: 70%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}
.headSpan11 {
    text-align: center;
    font: normal normal 600 11px/16px Open Sans;
    letter-spacing: 0px;
    color: #C4C4C4;
    opacity: 1;
    margin-left: 12px;
}
.headSpan3 {
    text-align: center;
    font: normal normal 600 11px/16px Open Sans;
    letter-spacing: 0px;
    color: #C4C4C4;
    opacity: 1;
    margin: 5px 7px 0px 0px;
}
.downArrow {
    float:right;
    margin-right:8px;
}
.arrowsAdjustment {
    margin-right: 6px;
}
.cardBodyProp {
    padding: 11px 13px 12px 8px;
    border-bottom: 0.5px solid #C4C4C4;
    width: 100%;
}
.cardBodyHead {
    display: flex;
    flex-direction: row;
    flex: 1;
}
