.collapseCard .configuration {
    border-radius : unset !important;
    /* /* border-top : none !important; */
    caret-color: transparent;
    border-top: 0.5px solid #d2d2d2;
    /* border-bottom: none!important; */
    margin:0 ;
}
.collapseCard .configuration > *{
    margin:1px 0;
}
.collapseCard .configurationHeader {
    height:36px;
    text-align: left;
    padding:0.75rem 0 !important;
    /* font: normal normal 600 12px/17px Open Sans; */
    font-family: Open Sans;
    font-size: 12px;
    font-weight: 600;
    line-height: 14px;
    letter-spacing: 0px;
    color: #202020;
    opacity: 1;
    /* font-weight:bold; */
    margin:0;
    cursor: pointer;
    background-color:#E6E6EB;
    /* background: #E6E6EB 0% 0% no-repeat padding-box; */
    /* border: 0.5px solid #D2D2D2; */
    
}
.collapseCard .configurationHeaderDiv{
    min-width: 243px;
    margin:auto;
    padding-right: 10px;
    display:flex;
    justify-content: space-between;
    align-items: center;
    margin-left: 12px;
    color:#202020;
    
    /* width:100%; */
}
.collapseCard .configurationHeaderDiv1{
    min-width: 243px;
    margin:auto;
    padding-right: 10px;
    display:flex;
    justify-content: space-between;
    align-items: center;
    margin-left: 12px;
    color:#202020;
    
    /* width:100%; */
}
@media (max-width: 910px) {
    .configurationHeaderDiv1{
        min-width:231px !important
    }
  }
.collapseCard .headingSpan{
    max-width: 90%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;;
}
.collapseCard .downArrow{
    float:right;
    /* margin-right:8px; */
}
.collapseCard .cardBodyProperties {
    padding : 6px 0px !important;
}
.collapseCard .cardBodyProperties > *{
    padding: 6px 10px;
    /* border: none; */
}
.collapseCard .arrowsAdjustment{
    margin-right: 6px;
}