.saveButton{
    background: #FFFFFF 0% 0% no-repeat padding-box;
    border: 1px solid #C4C4C4;
    border-radius: 2px;
    opacity: 1;
    /* width: 46px; */
    padding: 0 5px;
    height: 27px;
    margin-top: 4px;
    /* margin-right:18px; */
    margin-right:4px;
    margin-bottom: 4px;
    display: flex;
    cursor: pointer;
}

.saveBtnLabel{
    font-family: 'Open Sans';
    font-size: 12px;
    line-height: 17px;
    margin:auto;
    color: #404040;
}