.backdropDivImport{
    width : 513px;
    height : 267px;
    padding-top: 10px;
	background: #FFFFFF 0% 0% no-repeat padding-box;
	box-shadow: 0px 0px 10px #51515180;
	border: 1px solid #C4C4C4;
	opacity: 1;
    /* padding-left: 5px;
    padding-right: 5px; */
    padding-bottom: 30px;
    border-radius: 2px;
    position: relative;
}

.headerSectionStyle{
    /* box-shadow: "0px 0px 1px grey"; */
    height: 36px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0px 0px;
    border-bottom: 1px solid #E4E4E4;
}

.importSectionHeader{
    
    text-align: left;
    letter-spacing: 0px;
    color: #000000;
    opacity: 1;
    font-size: 14px;
    font-weight: 800;
    padding-left: 5px;
    margin-bottom: 10px !important;
}

.importSectionBody{
    margin: 10px;
}

.sectionContent{
    letter-spacing: 0px;
    color: #000000;
    font-size: 12px;
    /* font-weight: bold !important; */
    margin-top: 4px;
    margin-left: 8px;
    text-align: left;
    opacity: 1;
}

.sectionContentBody{
    width: 476px;
    height: 31px;
    background: #F0F0F0 0% 0% no-repeat padding-box;
    border: 1px solid #C4C4C4;
    border-radius: 2px;
    /* opacity: 0.4; */
}

.sectionContentBackground{
    width: 476px;
    height: 31px;
    margin-top: 5px !important;
    background: #F0F0F0 0% 0% no-repeat padding-box;
    border: 1px solid #C4C4C4;
    border-radius: 2px;
    opacity: 0.4;
}

.importOptionsSection{
    color: black;
    font-size: 15px;
    margin-bottom: 5px;
}

.newSection{
    width: 141px;
    height: 40px;
    border: 1px solid #0072C6;
    border-radius: 2px 0px 0px 2px;
    opacity: 1;
    color: #606060;
    font-size: 10px;
    text-align: center;
    /* justify-content: center; */
}

.sectionRow{
    display: flex;
    flex-wrap: no-wrap;
}

.buttonsPart{
    margin-top:10px !important;
    margin-left: 14px !important;
    margin-bottom:12px !important;
}

.firstButtonImport{
    width: 123px;
    height: 40px;
    background: #FFFFFF 0% 0% no-repeat padding-box;
    border: 1px solid #DADADA;
    border-radius: 2px;
    opacity: 1;
}

.firstButtonImportAfterClick{
    width: 123px;
    height: 40px;
    border: 1px solid #0072C6;
    background: #FFFFFF 0% 0% no-repeat padding-box;
    border-radius: 2px;
    opacity: 1;
}

.firstButtonLabel{
    text-align: center;
    margin-top: 9px;
    color: #606060;
    font-size: 11px;
}

.firstButtonLabelAfterClick{
    text-align: center;
    margin-top: 9px;
    color: #0072C6;
    font-size: 11px;
}

.fileNameSection{
    margin-bottom: 4px;
    width: 233px;
    height: 28px;
    background: #F8F8F8 0% 0% no-repeat padding-box;
    opacity: 1;
    /* padding-bottom: 4px !important; */
    display: flex;
    /* padding-top: -9px; */
    padding-left: 7px;
    padding-top: 4px;
}

.redCrossSection{
    margin-left: auto;
    order:2;
    margin-right: 5px;
    margin-top: -4px;
    /* right: 0px;
    margin-top: 10px;
    margin-left: 40px !important; */
}

.footerSectionStyle{
    width: 100%;
    height: 50px !important;
    color: gray;
    position: absolute;
    bottom: 0px;
  /* box-shadow: 0px 1px 10px grey; */
    background: #F5F5F5;
    justify-content: flex-end !important;
}

.cancelButton{
    background-color: #FFFFFF !important;
    width: 54px;
    height: 28px;
    font-size: 12px !important;
    text-transform:none !important;
    border-radius:2px;
    border:1px solid #C4C4C4;
}

.importButton{
      background-color: #0072c6 !important;
      color: white !important;
      width: 43px;
      height: 28px;
      font-size: 12px !important;
      text-transform:none !important;
      border-radius:2px;
}

.fileNameStyle{

    text-align: left;
    text-decoration: underline;
    /* font: normal normal normal 9px/13px Open Sans; */
    font-family: Open Sans;
    font-size: 12px;
    letter-spacing: 0px;
    color: #2F5BBF;
    opacity: 1;
    /* padding-bottom: 10px !important; */
    /* margin-top: 10px !important; */
    /* margin-bottom: 2px; */
}