.errorMessageDiv{
    width:100%;
    padding-top: 1px; 
    color:#D53D3D;
    font: normal normal normal 11px/14px Open Sans;
}
.borderStyle{
    border-color:#D53D3D;
}
.tooltipMainErrormessage{
    position:absolute;
    caret-color:transparent;
    top: 15px;
    margin: 0px 2px;
    display:inline-block;
}
.toolTipErrormessage{
    padding:0 3px;
    display:inline-block
}
.tooltip2Errormessage {
    position:absolute;
    width: 0;
    height: 0;
    opacity:0;
}
.tooltipIconErrormessage:hover ~ .tooltip2Errormessage,.tooltipMainErrormessage:hover .tooltip2Errormessage,
.spacingThemeControls:hover .tooltip2Errormessage {
    z-index: 1;
    opacity:1;
    position: absolute;
    left:0;
    min-width: 100px;
    width: 250px;
    height: auto;
    padding: 5px;
    font: normal normal 600 12px/16px Open Sans ;
    text-align: center;
    color: #D53D3D;
    background: #fff;
    box-shadow: 0px 3px 6px #00000029;
    border: 1px solid #E7E7E7; 
}
.ttErrormsg {
    z-index: 1;
    opacity:1;
    position: absolute;
    left:0;
    min-width: 100px;
    width: 250px;
    height: auto;
    padding: 5px;
    font: normal normal 600 12px/16px Open Sans ;
    text-align: center;
    color: #D53D3D;
    background: #fff;
    box-shadow: 0px 3px 6px #00000029;
    border: 1px solid #E7E7E7; 
}
.tooltip2Errormessage:after {
    content: "";
    position: absolute;
    border-width: 10px;
    border-style: solid;
    border-color: #fff transparent transparent transparent;
}

/* Towards Right */
.tooltipLeftErrormessage{
    right:2em;
    transform: translate(0,-100%);
}
.tooltipLeftErrormessage:after{
    right:-1.5em;   
    transform:rotate(-90deg);
}

/* Towards Left */

.tooltipRighErrormessaget{
    left:2em;
    transform: translate(0,-100%);
}
.tooltipRightErrormessage:after{
    left:-1.5em;    
    transform:rotate(90deg); 
}


 /* Towards Top */
.tooltipBottomErrormessage{
    top:2em;
    transform: translate(-50%,0);
}
.tooltipBottomErrormessage:after{
    top:-1.5em;
    transform:rotate(180deg);
    left:50%; 
}

/* Towards Bottom */
.tooltipTopErrormessage{
    bottom:0px;
}
.tooltipTopErrormessage:after{
    bottom:-1.5em;
    left:50%;
}
.a_errMsgCtrlStyle1-3{
    border : 1px solid #D53D3D !important;
}
.a_errMsgCtrlStyle2{
    border-bottom : 1px solid #D53D3D !important;
}