.dataAdditionRowTextbox {
    width:100%;
	height: 100%;
    padding: 4px;
    border: none;
    background: inherit;
    color: inherit;
    border-radius: 0px;
    text-overflow: ellipsis;
}

.dropDownBorderRemover {
    border : none !important;
    background-color: transparent !important;
}

.onFocusOutlineRemover:focus { 
    border: none !important;
  box-shadow: none !important;
  outline: none !important;
}

.dataAdditionRowTextarea {
    width:100%;
	height: 100%;
    padding: 4px;
    border: none;
    background: inherit;
    color: inherit;
    border-radius: 0px;
    text-overflow: ellipsis;
    outline: none;
}

.dataAdditionRowTextareaDiv {
    width: 100%;
    max-height:111px;
    height: 111px;
    background: #FFFFFF 0% 0% no-repeat padding-box;
    box-shadow: 0px 3px 6px #00000015;
    position:absolute;
    left:0px;
    top:0px;
    opacity: 1;
    z-index: 1000;
}
.editableTextArea {
    max-height:100%;
    height:100%;
    width: 100%;
    resize:none;
    overflow-y: auto;
    /* border:1px solid #0072c6; */
    opacity: 1;
}
.dataAdditionRowCheckbox{
	display: flex;
    margin: auto;
}
.dataAdditionRowRadio{
	margin:0px;
	display: flex;
	align-items: center;
}
.dataAdditionRowRadio {
    border: 1px solid #C4C4C4 ;
    margin: 4px;
    border-left: none;
    border-radius: 2px;
}
.dataAdditionRowRadioDiv{
	height:28px;
    /* padding: 3px; */
    display: flex;
    flex:1;
    align-items:center;
    justify-content: center;
    border-left: 1px solid #C4C4C4 ;
    text-overflow: ellipsis;
}
.dataAdditionRadioSelectedOption {
    background-color : #0072C6;
    color: #FFFFFF;
    border : 1px solid #0072C6;
}
.dataAdditionRowRadioLabel{
	margin-left: 4px;;
	margin-bottom: 0px;
}
