.variableNameStyle{
    margin-top: 2px;
    margin-left: 4px;
    color: #000000;
    font-size: 13px;
    width: 120px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    cursor: pointer;
    text-align: start;
}


.parentNameStyle{
    margin-top: 2px;
    margin-left: 4px;
    color: #000000; 
    font-weight: 600;
    font-size: 13px;
    cursor: pointer;
}

.mapButtonStyle{
    margin-left: 25px !important;
    background-color:#0072c6;
    color:#ffffff;
    border:none;
    border-radius:2px;
    height:22px;
    font-size:10px;
    margin-right: 3px;
    margin-top: 2px;
}

.parentChild{
    /* padding: 0.40rem 1.25rem; */
    padding: 0.20rem 1.25rem;
}

.unMapButtonStyle{
    margin-left:auto;
     color:red; 
     font-size:10px;
     margin-top: 4px;
     cursor: pointer;
    /* margin-right:14px !important; */
}

.iconStyle{
    cursor: pointer;
    margin-right: 8px;
    margin-left: -12px
}

.variableTypeClass{
  color: #053253;
  margin-top: 3px;
  font-size: var(--base_text_font_size) !important;
}
