.undoRedoMain{
    display: flex;
    /* border: 1px solid #A8A8A8; */
    border-radius: 2px;
    height:32px;
    width:45px;
    align-items: center;
    margin:auto;
    /* margin-left: 34px; */
    cursor:pointer;
    justify-content: space-between !important;
}
.undoRedoBorder{
    border-left:1px solid #575757;
    padding:10px 0;    
}
.undoRedo{
    padding:6px 8px;
}
.undoRedo:hover{
    background: #FEEDE2 0% 0% no-repeat padding-box;
    border: 1px solid #FA6400;
}
