
.percent {
    border: 1px solid #bbb;
    border-radius: 2px;
    width: 30%;
    height: 24px;
}

.percent input {
    border: none;
    outline: none;
    text-align:"left";
    width: 86%;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
}