.UndoRedo {
    padding: 10px;
    /* border: 1px solid gray; */
    font-size: 20px;
    display: flex;
    text-align: center;
    box-sizing: border-box;
    color: gray;
}

.UndoRedo .UndoBtn,
.UndoRedo .RedoBtn {
    height: 30px;
    width: 30px;
    margin: auto;
}

.UndoBtn:hover,
.RedoBtn:hover {
    height: 30px;
    width: 30px;
    color: blueviolet;
    cursor: pointer;
}
.undoRedoBtn{
    margin-left:5px;
    margin-right:5px;
}

.btnDivider {
    border: 1px solid #DADADA;
    opacity: 1;
    height: 21px;
    margin-left:14px;
    margin-right:14px;
}