.FormOptions {
    padding: 10px;
    /* border: 1px solid gray; */
    font-size: 20px;
    display: flex;
    text-align: center;
    box-sizing: border-box;
    color: gray;
}
.previewButton{
    background: #FFFFFF 0% 0% no-repeat padding-box;
    border: 1px solid #C4C4C4;
    border-radius: 2px;
    opacity: 1;
    /* width: 86px; */
    height: 27px;
    margin-top: 4px;
    margin-right: 8px;
    margin-bottom: 4px;
    display: flex;
    cursor: pointer;
    font-family: 'Open Sans';
    font-size: 12px;
}
.previewLink{
    text-decoration: none !important;
    border:0px;
}
.previewBtnIcon{
    margin:auto;
    width: 15px;
    /* height: 9px; */
    color: #606060;
}
.previewBtnLabel{
    font-family: 'Open Sans';
    font-size: 12px;
    line-height: 17px;
    margin:auto;
    color: #606060;
    cursor: pointer;
}
.previewBtnLabel :hover {
    text-decoration: none;
}