.deviceTypeSelector{
    display:flex;
    /* padding:10px; */
}
.deviceTypeSelector_sub{
    /* height:40px; */
    border: 1px solid #A8A8A8;
    border-right: 0;
    /* border-radius: 2px; */
    text-align: center;
    padding:7px;
    cursor:pointer;
    display:flex;  
    justify-content:center; 
    align-items:center;
    background: #FFFFFF 0% 0% no-repeat padding-box;
    opacity: 1;
    height: 35px;
    width: 35px;
}
.deviceTypeSelector_sub_active{
    background: #FEEDE2 0% 0% no-repeat padding-box;
    border: 1px solid #FA6400;
    /* height: 35px; */
}
.deviceTypeSelector_sub_last{
    border-right: 1px solid #A8A8A8;
}
.deviceTypeSelector_sub:hover{
    background: #FEEDE2 0% 0% no-repeat padding-box;
    border: 1px solid #FA6400;
}

