.propertiesDiv{
    width:100%;
    height: 105px;
    /* border: 1px solid var(--unnamed-color-c4c4c4); */
    background: #F8F8F8 0% 0% no-repeat padding-box;
    box-shadow: 0px 3px 6px #00000029;
    border: 1px solid #C4C4C4;
    opacity: 1;
    margin-top:12px;
    cursor: pointer;
}
.iconDiv{
    margin-bottom: 6;
    margin-left: auto;
    margin-right: auto;
}
.labelDiv{
    font-size: 12px;
    font-family: 'Open Sans';
    color:#000000;
    line-height: 17px;
    text-align: left;
    transform: matrix(0, 1, -1, 0, 0, 0);
    text-align: left;
    /* font: normal normal normal 12px/17px Open Sans; */
    letter-spacing: 0px;
    color: #000000;
    opacity: 1;
}
